import lodash from 'lodash'

import { CATEGORIES } from '../constants'
import { getTransformationOptions } from './transformationOptions'
import { transformToNested } from './transformToNested'

const { each, has } = lodash

const transformFromConfigsToResponse = (configs, callback = null) => {

  const response = {}

  each(configs, (config, key) => {

    let value = has(config, `value`) ? config.value : config.default

    if (callback) {

      const {
        key: newKey,
        value: newValue,
      } = callback(key, config)

      if (newKey) {
        key = newKey
      }

      if (newValue) {
        value = newValue
      }
    }

    response[key] = value
  })

  return response
}

const transformToConfigValues = (configs, opts = {}) => {

  const options = getTransformationOptions(opts)

  const response = transformFromConfigsToResponse(configs, (key, config) => {

    key = options.categorized ? `${CATEGORIES[config.category].plural}.${key}` : key

    return {
      key,
    }
  })

  if (options.nested) {
    return transformToNested(response)
  }

  return response
}

export { transformToConfigValues }
