import lodash from 'lodash'

import type {
  Config,
  StandardizedConfigs,
} from './types'

const { each, isBoolean, isObject, last, split, startsWith, trimEnd } = lodash

/**
 * TODO: This function doesn't know how to handle configs with an empty array value (preSelectedNewsletters) and the config will just be ignored
 */
const flattenJsonObject = (object: object, initialPathPrefix: string = ``) => {

  function flatten(object, initialPathPrefix) {
    if (!object || typeof object !== `object`) {
      return [{ [initialPathPrefix]: object }]
    }

    const prefix = initialPathPrefix
      ? Array.isArray(object)
        ? initialPathPrefix
        : `${initialPathPrefix}.`
      : ``

    return Object.keys(object)
      .flatMap((key) =>
        flatten(
          object[key],
          Array.isArray(object) ? `${prefix}[${key}]` : `${prefix}${key}`,
        ),
      )
      .reduce((acc, path) => ({ ...acc, ...path }), [])
  }

  const flattenedObject = flatten(object, initialPathPrefix)

  const configs = {}
  each(flattenedObject, (value, key) => {
    const splitKey = split(key, `.`)
    const lastPart = last(splitKey)
    if (startsWith(lastPart, `#`)) {
      if (lastPart === `#value`) {
        configs[trimEnd(key, `.#value`)] = value
      }
    } else {
      configs[key] = value
    }
  })

  return configs
}

const createConfigsFromJson = (json: object): StandardizedConfigs => {

  const configs = {}

  const flattenJson = flattenJsonObject(json)

  each(flattenJson, (value, key) => {

    let defaultValue = value
    if (isObject(value)) {
      defaultValue = value[`#value`]
    }

    const config: Config = {
      default: defaultValue,
    }

    if (isBoolean(defaultValue)) {
      config.type = `switch`
    }

    configs[key] = config
  })

  return configs
}

export { createConfigsFromJson }
