import lodash from 'lodash'

const { assign, mapValues } = lodash

export type TransformationOptions = {
  nested: boolean,
  categorized: boolean,
}

const getTransformationOptions = (options: Partial<TransformationOptions> = {}): TransformationOptions => {

  options = mapValues(options, () => true)

  const opts: TransformationOptions = {
    nested: false,
    categorized: false,
  }

  return assign(opts, options)
}

export { getTransformationOptions }
